import { Component, Input, OnChanges, SimpleChanges, ViewChild, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Thin2Chart } from 'thin2-chart';
import { Global } from '../../../../../global';
import { EntityNameDictionary, EntityColorDictionary } from '../../../model/entity.enum';
import { DashboardService } from '../../dashboard.service';
import { Common } from '../../../../utils/configuration/app/common';

@Component({
    selector: 'pie-chart-entity-component',
    templateUrl: './pie-chart-entity.component.html'
})
export class PieChartEntityComponent implements OnChanges, OnInit {
    @ViewChild(Thin2Chart)
    public chart: Thin2Chart;

    @Input()
    public dataset: any[];
    private pieDataset: any[];

    constructor(
        private dashboardService: DashboardService,
        private activatedRoute: ActivatedRoute
    ) {}

    public ngOnInit() {
        this.activatedRoute.queryParams.subscribe((param) => {
            this.buildChart();
        });
    }

    // 当数据绑定输入属性的值发生变化的时候，Angular 将会主动调用 ngOnChanges 方法。
    // 它会获得一个 SimpleChanges 对象，包含绑定属性的新值和旧值，它主要用于监测组件输入属性的变化。
    public ngOnChanges(changes: SimpleChanges): void {
        this.dataset = changes.dataset.currentValue;
        this.buildChart();
    }

    public buildChart() {
        if (this.dataset) {
            this.pieDataset = this.dataset.map(this.datasetAdapter);

            // 过滤掉不在查询条件中的entity信息，因为有些新闻属于多个entity
            const entityList = this.dashboardService.getLastFilter().entity;

            if (entityList.indexOf('all') < 0) {
                const data = [];

                this.pieDataset.forEach((v) => {
                    if (entityList.indexOf(v.name.toLowerCase()) >= 0) {
                        data.push(v);
                    }
                });

                this.pieDataset = data;
            }

            this.drawChart();
        }
    }

    public drawChart() {
        const options = {
            chart: {
                plotBackgroundColor: null,
                plotBorderWidth: null,
                plotShadow: false,
                type: 'pie'
            },
            title: {
                text: ''
            },
            tooltip: {
                formatter() {
                    return `<b>${this.point.name}</b> <br> <b>${
                        Common.pieChartEntityFields[Global.language].tooltip
                    }: ${this.y}</b>`;
                }
            },
            accessibility: {
                point: {
                    valueSuffix: ''
                }
            },
            plotOptions: {
                pie: {
                    allowPointSelect: true,
                    cursor: 'pointer',
                    dataLabels: {
                        enabled: true,
                        formatter() {
                            return `<b>${this.point.name} (${this.y})</b>`;
                        }
                        // format: '<b>SDG: {point.name} ({y})</b>'
                    }
                }
            },
            series: [
                {
                    name: '',
                    data: this.pieDataset
                }
            ]
        };

        this.chart.draw(options);
    }

    // 将Statistics类型转换成饼图需要的类型，主要是获取每个ODS扇形的颜色
    public datasetAdapter = (item) => ({
        name:
            item.entity.toLowerCase() === 'others'
                ? Common.pieChartEntityFields[Global.language].others
                : EntityNameDictionary[Global.language][item.entity],
        color: EntityColorDictionary[item.id],
        y: Number(item.total)
    });
}
