import { Component, Input, OnChanges, SimpleChanges, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SDGNameDictionary, SDGColorDictionary } from '../../../model/sdg.enum';
import { Thin2Chart } from 'thin2-chart';
import { Global } from '../../../../../global';
import { Common } from '../../../../utils/configuration/app/common';

@Component({
    selector: 'pie-chart-component',
    templateUrl: './pie-chart.component.html'
})
export class PieChartComponent implements OnChanges, OnInit {
    @ViewChild(Thin2Chart)
    public chart: Thin2Chart;

    @Input()
    public dataset: any[];
    private pieDataset: any[];

    constructor(private activatedRoute: ActivatedRoute) {}

    public ngOnInit() {
        this.activatedRoute.queryParams.subscribe((param) => {
            if (this.dataset) {
                this.pieDataset = this.dataset.map(this.datasetAdapter);
                this.drawChart();
            }
        });
    }

    // 当数据绑定输入属性的值发生变化的时候，Angular 将会主动调用 ngOnChanges 方法。
    // 它会获得一个 SimpleChanges 对象，包含绑定属性的新值和旧值，它主要用于监测组件输入属性的变化。
    public ngOnChanges(changes: SimpleChanges): void {
        this.dataset = changes.dataset.currentValue;
        if (this.dataset) {
            this.pieDataset = this.dataset.map(this.datasetAdapter);
            this.drawChart();
        }
    }

    public drawChart() {
        const options = {
            chart: {
                plotBackgroundColor: null,
                plotBorderWidth: null,
                plotShadow: false,
                type: 'pie'
            },
            title: {
                text: ''
            },
            tooltip: {
                formatter() {
                    return `<b>${this.point.name}. ${
                        SDGNameDictionary[Global.language][this.point.name]
                    }</b> <br> <b>${Common.pieChartFields[Global.language].tooltip}: ${this.y}</b>`;
                }
            },
            accessibility: {
                point: {
                    valueSuffix: ''
                }
            },
            plotOptions: {
                pie: {
                    allowPointSelect: true,
                    cursor: 'pointer',
                    dataLabels: {
                        enabled: true,
                        formatter() {
                            return `<b>${this.point.name}. ${
                                SDGNameDictionary[Global.language][this.point.name]
                            } (${this.y})</b>`;
                        }
                        // format: '<b>SDG: {point.name} ({y})</b>'
                    }
                }
            },
            series: [
                {
                    name: '',
                    data: this.pieDataset
                }
            ]
        };

        this.chart.draw(options);
        console.log(options);
    }

    // 将Statistics类型转换成饼图需要的类型，主要是获取每个ODS扇形的颜色
    public datasetAdapter = (item) => ({
        name: item.ods,
        color: SDGColorDictionary[item.ods],
        y: Number(item.total)
    });
}
